.carousel-caption
{
  top:25%;
  left:10%;
  right:10%;
  text-align: left;
}
.carousel-caption > h3
{
color:#d9b22b;
font-size:3.2em;
text-transform: uppercase;
}
.carousel-caption > p
{
color:#000000;
font-size:1.3em;
text-transform: uppercase;
}
.carousel-caption > button
{
  background-color: #d9b22b;
  color:#ffffff;
  border: 1px solid #d9b22b;
}
.carousel-control-next, .carousel-control-prev
{
  width:5%;
}